function itemize(iteM, widtH, coloR, X, Y) {
   let item = iteM; // Directional array
   let count = item.length; // Number of squares
   let width = widtH; // Width of one square
   let color = coloR; // Object containing r, g, and b values
   let x = X; // Starting x
   let y = Y; // Starting y
   fill(color.r, color.g, color.b);
   noStroke();
   rect(x, y, width, width);
   for (let i = 0; i < count; i++) {
      if (item[i] === 0) {
         x -= width;
      } else if (item[i] === 1) {
         y -= width;
      } else if (item[i] === 3) {
         x += width;
      } else if (item[i] === 2) {
         y += width;
      } else {
         console.error(`Itemize(): Directional value out of bounds\n\t${item[i]} !== 0, 1, 2, or 3`);
      }
      rect(x, y, width, width);
   }
}

